* {
  font-family: sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

h2{
  font-size: 18px;
  padding: 10px 20px;
  color: #575ed8;
}

.container {
  width: 100%;
  min-height: 100%;
  margin: 0px auto;
}

.main-messenger{
  max-width: 600px;
  margin: 30px auto;
  border: 1px solid #ddd;
  -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
          box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
  border-radius: 2px;
}

#chat-window{
  height: 400px;
  overflow: auto;
  background: #f9f9f9;
}

#output p{
  padding: 14px 0px;
  margin: 0 20px;
  border-bottom: 1px solid #e9e9e9;
  color: #555;
}

#feedback p{
  color: #aaa;
  padding: 14px 0px;
  margin: 0 20px;
}

#output strong{
  color: #575ed8;
}

label{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  padding: 10px 20px;
  color: #555;
}
textarea {
  padding: 10px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #eee;
  border: 0;
  display: block;
  width: 100%;
  background: #fff;
  border: 1px solid #ddd;
  -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
          box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
  font-family: Nunito;
  font-size: 16px;
}
input{
  padding: 10px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #eee;
  border: 0;
  display: block;
  width: 100%;
  background: #fff;
  border: 1px solid #ddd;
  -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
          box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
  font-family: Nunito;
  font-size: 16px;
}

button{
  background: #575ed8;
  color: #fff;
  font-size: 18px;
  border: 0;
  margin: 2em 0px;
  padding: 12px 0;
  width: 100%;
  border-radius: 0 0 2px 2px;
}

#handle {
  padding: 10px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #eee;
  border: 0;
  display: block;
  width: 100%;
  background: #fff;
  border: 1px solid #ddd;
  -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
          box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
  font-family: Nunito;
  font-size: 16px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media (min-width: 600px) {
  .container { width: 600px; padding: 0px 10px;}
}





